import './index.css'

const Skills = () =>{

    const skillsTriggered = () =>{
        document.querySelector('.skills-content').classList.remove('skills-slide')
        document.querySelector('.skills').classList.add('fade-out')

        setTimeout( () => {
            document.querySelector('.skills').classList.add('hide')
        }, 850);
    }

    return (
        <div className='skills hide fade-out' onClick={skillsTriggered}>
            <div className='skills-content' onClick={ e => e.stopPropagation() }>
                <ul>
                    <label> Latest Technologies learned: </label>
                    <li> Ruby on Rails </li>
                    <li> Mongo DB </li>
                    <li> Cypress.io </li>
                    <li> Storybook </li>
                    <li> Jest </li>

                    <label> Love to use: </label>
                    <li> ES6 Javascript </li>
                    <li> React </li>
                    <li> Redux </li>
                    <li> Sass </li>

                    <label> Previously Exposed to: </label>
                    <li> Java </li>
                    <li> Swift </li>
                    <li> Python </li>
                    <li> Typescript </li>
                    <li> Firebase </li>
                </ul>
            </div>
        </div>
    )
}

export default Skills