import './index.css'

const About = () => {
    const triggerCloseAbout = () => {
        document.querySelector('.about').classList.remove('about-slide')
        document.querySelector('.about-container').classList.remove('about-container-slide')
        document.querySelector('.profile-pic').classList.remove('profile-pic-about')
    }

    return (
        <div className='about'>

            <div className='about-container'>
                <button onClick={triggerCloseAbout} className='close-about'> X </button>

                <div className='about-content'>

                    <h2> About me </h2>

                    <p>
                    My name is Michael Paguay. I am 32 years old and I live in New York City. I love cycling, running, good design, visual arts, learning, and creating.
                    </p>

                    <p>
                    My love of learning and creating has manifested itself into a deep interest in computer programming, which I am aiming to turn into a career. I have previously developed skills in drawing, digital graphics, photography, video editing, animation, and 3D modeling.
                    </p>

                    <p>
                    I feel like the collage I made of screenshots in the background of this webpage does a better job of describing me and my interests than I could ever do with words, but if I had to choose a phrase to describe myself, I would go with: 
                    </p>

                    <h4>
                        self taught programmer, <br/>
                        traditionally educated designer, <br/>
                        experienced educator
                    </h4>

                    <h3> Self-taught programmer: </h3>
                    <p>
                    I have dabbled in the world of programming for 8 years and I have taken programming seriously for the last 2 years. In my personal time, I use the resources like YouTube and Google to help me learn the skills necessary to create the programs I am interested in making.
                    </p>

                    <h3> Traditionally-educated designer: </h3>

                    <p>
                    I have a Bachelor’s and Associate’s degree in Computer Graphics and Design from the City University of New York. At one point in my life, I wanted to be a graphic designer. However, the appeal of that career wore off after I realized that the skills necessary to do graphic design were not challenging enough technically or creatively and did not allow me to express myself meaningfully.
                    </p>

                    <h3> Experienced educator: </h3>

                    <p>
                    In my search for a meaningful career, I happened upon a career as a Paraprofessional for the New York City Department of Education. For the last 8 years, I have worked alongside teachers and other paraprofessionals to ensure the best possible education for my special needs students. My students are all in elementary school and have been clinically diagnosed as Emotionally Disturbed; these children have all been previously removed from other schools due to behavior issues. They have been diagnosed with conditions ranging from attention-deficit/hyperactivity disorder(ADHD), oppositional defiant disorder (ODD) and autism (ASD). Although the thought of working with “rough” students might turn off some, my students actually inspire me. Seeing them overcome incredible mental and emotional hurdles as they develop as people has kept me dedicated to my work for the last 8 years. They almost inspired me to become a teacher. However, despite education being a decent fit for me, it wasn’t perfect. I have always felt like being involved with education meant that I had to leave behind my artistic passion for creating things in order to be of service to others.
                    </p>

                    <p>
                    Although my job as a paraprofessional does make me feel like I am contributing to society, I have come to understand that I need to find a career that will allow me not only to exercise my passion for helping society, but also exercise my other passions for creativity and artistic self-expression. After years of using programming as a creative outlet after work, I now realize that a career in programming would allow me to find the challenge, inspiration, creativity, and sense of accomplishment that I desperately crave all while fulfilling my deepest desire: to help others.
                    </p>

                </div>
            </div>

        </div>
    )
}

export default About